
import { defineComponent } from 'vue';

interface State {
  activeTab: string;
}

export default defineComponent({
  name: 'Navbar',
  props: {
    selectedTab: {
      type: String,
      required: true
    }
  },
  emits: ['clickedTab'],
  data() {
    return {
      activeTab: this.$props.selectedTab
    } as State;
  }
});
